import React, { useCallback } from "react";

import DraggableProfessionalSummaryItem from "./DraggableProfessionalSummaryItem";

import CommonSwitcher from "@/app/common-components/CommonSwitcher";
import CommonAddButton from "@/app/common-components/buttons/CommonAddButton";
import { useCvBuilderStore } from "@/store/useCvBuilderStore";

interface ProfessionalSummary {
  id: string;
  title: string;
  description: string;
  isVisible: boolean;
  order: number;
}

const ProfessionalSummary: React.FC = () => {
  const {
    professionalSummary,
    addProfessionalSummary,
    updateProfessionalSummary,
    deleteProfessionalSummary,
    reorderProfessionalSummary,
  } = useCvBuilderStore();

  const sortedSummaries = [...professionalSummary].sort((a, b) => a.order - b.order);

  const handleAddSummary = useCallback(() => {
    addProfessionalSummary({
      title: "–ù–æ–≤–∏–π –∑–∞–ø–∏—Å",
      description: "",
      isVisible: true,
    });
  }, [addProfessionalSummary]);

  const handleMoveSummary = useCallback(
    (dragIndex: number, hoverIndex: number) => {
      const draggedSummary = sortedSummaries[dragIndex];
      const newSummaries = [...sortedSummaries];

      // –í–∏–¥–∞–ª—è—î–º–æ –µ–ª–µ–º–µ–Ω—Ç –∑ –ø–æ—á–∞—Ç–∫–æ–≤–æ—ó –ø–æ–∑–∏—Ü—ñ—ó
      newSummaries.splice(dragIndex, 1);
      // –í—Å—Ç–∞–≤–ª—è—î–º–æ –π–æ–≥–æ –≤ –Ω–æ–≤—É –ø–æ–∑–∏—Ü—ñ—é
      newSummaries.splice(hoverIndex, 0, draggedSummary);

      reorderProfessionalSummary(newSummaries);
    },
    [sortedSummaries, reorderProfessionalSummary]
  );

  const handleUpdateSummary = useCallback(
    (id: string, updates: Partial<ProfessionalSummary>) => {
      updateProfessionalSummary(id, updates);
    },
    [updateProfessionalSummary]
  );

  const handleDeleteSummary = useCallback(
    (id: string) => {
      deleteProfessionalSummary(id);
    },
    [deleteProfessionalSummary]
  );

  return (
    <div className="professional-summary__component">
      <CommonSwitcher
        labelText="–£–≤—ñ–º–∫–Ω—É—Ç–∏ —Å–µ–∫—Ü—ñ—é"
        className="mb-6"
      />

      <div className="summaries-list mb-4">
        {sortedSummaries.map((summary, index) => (
          <DraggableProfessionalSummaryItem
            key={summary.id}
            summary={summary}
            index={index}
            totalItems={sortedSummaries.length}
            onMove={handleMoveSummary}
            onUpdate={handleUpdateSummary}
            onDelete={handleDeleteSummary}
          />
        ))}
      </div>

      <CommonAddButton
        buttonText="–î–æ–¥–∞—Ç–∏ –Ω–æ–≤–∏–π –∑–∞–ø–∏—Å"
        onClick={handleAddSummary}
      />
    </div>
  );
};

export default ProfessionalSummary;
