import React, { useState } from "react";
import { Button } from "@heroui/react";
import { Trash2, ArrowUp, ArrowDown } from "lucide-react";

import CommonInfoDropdownItem from "@/app/common-components/CommonInfoDropdownItem";
import CommonTextArea from "@/app/common-components/CommonTextArea";
import { DragDotsIcon } from "@/components/icons";

interface ProfessionalSummary {
  id: string;
  title: string;
  description: string;
  isVisible: boolean;
  order: number;
}

interface DraggableProfessionalSummaryItemProps {
  summary: ProfessionalSummary;
  index: number;
  totalItems: number;
  onMove: (dragIndex: number, hoverIndex: number) => void;
  onUpdate: (id: string, updates: Partial<ProfessionalSummary>) => void;
  onDelete: (id: string) => void;
}

const DraggableProfessionalSummaryItem: React.FC<DraggableProfessionalSummaryItemProps> = ({
  summary,
  index,
  totalItems,
  onMove,
  onUpdate,
  onDelete,
}) => {
  const [isDragging, setIsDragging] = useState(false);

  const handleTitleChange = (newTitle: string) => {
    onUpdate(summary.id, { title: newTitle });
  };

  const handleDescriptionChange = (newDescription: string) => {
    onUpdate(summary.id, { description: newDescription });
  };

  const handleMoveUp = () => {
    if (index > 0) {
      onMove(index, index - 1);
    }
  };

  const handleMoveDown = () => {
    if (index < totalItems - 1) {
      onMove(index, index + 1);
    }
  };

  const handleDragStart = (e: React.DragEvent) => {
    setIsDragging(true);
    e.dataTransfer.setData("text/plain", index.toString());
  };

  const handleDragEnd = () => {
    setIsDragging(false);
  };

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    const draggedIndex = parseInt(e.dataTransfer.getData("text/plain"));
    if (draggedIndex !== index) {
      onMove(draggedIndex, index);
    }
  };

  return (
    <div
      className={`draggable-professional-summary-item mb-4 border rounded-lg p-4 ${
        isDragging ? "opacity-50" : "opacity-100"
      }`}
      draggable
      onDragStart={handleDragStart}
      onDragEnd={handleDragEnd}
      onDragOver={handleDragOver}
      onDrop={handleDrop}
    >
      <div className="flex items-center gap-2 mb-2">
        <div className="flex flex-col gap-1">
          <Button
            size="sm"
            variant="ghost"
            isIconOnly
            onClick={handleMoveUp}
            disabled={index === 0}
            className="h-6 w-6"
          >
            <ArrowUp size={12} />
          </Button>
          <div className="drag-handle cursor-move p-1 hover:bg-gray-100 rounded text-gray-400">
            <DragDotsIcon size={16} />
          </div>
          <Button
            size="sm"
            variant="ghost"
            isIconOnly
            onClick={handleMoveDown}
            disabled={index === totalItems - 1}
            className="h-6 w-6"
          >
            <ArrowDown size={12} />
          </Button>
        </div>

        <div className="flex-1">
          <input
            type="text"
            value={summary.title}
            onChange={(e) => handleTitleChange(e.target.value)}
            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            placeholder="–ó–∞–≥–æ–ª–æ–≤–æ–∫ —Ä–µ–∑—é–º–µ"
          />
        </div>

        <Button
          size="sm"
          color="danger"
          variant="ghost"
          isIconOnly
          onClick={() => onDelete(summary.id)}
          className="flex-shrink-0"
        >
          <Trash2 size={16} />
        </Button>
      </div>

      <CommonInfoDropdownItem title={summary.title || "–ù–æ–≤–∏–π –∑–∞–ø–∏—Å"}>
        <div className="my-4">
          <CommonTextArea
            labelText="–û–ø–∏—Å"
            value={summary.description}
            onChange={handleDescriptionChange}
          />
        </div>
      </CommonInfoDropdownItem>
    </div>
  );
};

export default DraggableProfessionalSummaryItem;
